@media screen and (min-width:1200px) {
    .content-header .search-container {
        max-width: 1200px;
        width: 1200px;
        left: auto !important;
        right: auto !important;
    }
}
body{
    margin: 0 8px;
}
body.show {
    overflow: hidden;
    margin: 0;
}
.head {
    background: var(--point-color);
    color: #fff;
    height: 90px;
    text-align: center;
    line-height: 90px;
    margin: 0 -8px 0 -8px;
}
body.show .head {
    box-shadow: 0 1px 14px 0 #00000059;
    z-index: 12;
}
.content {
    max-width: 1200px;
    width: 100%;
    align-self: center;
    position: relative;
}
body.show .content:not(.show) {
    height: calc(100% - 90px);
}
.content.scroll .content-header {
    top: -91px;
}
.content.scroll .content-header::before, .content.show .content-header::before {
    display: block;
}
.content.show .content-header {
    position: fixed;
    top: 0;
    max-width: 1200px;
    left: 50%;
    right: -50%;
    transform: translate(-50%, 0);
    padding: 0 8px;
    border-bottom: 1px solid #d5d5d5;
    z-index: 11;
    box-shadow: 0px 1px 14px 0px #00000059;
    overflow: hidden;
    transition: 0.1s;
}
.content.show .content-header::before {
    transform: translate(-50%, -50vw);
    background: transparent;
    z-index: 0;
    width: 110vw;
    height: 110vw;
}
.content.scroll .tag-anchor, .content.show .tag-anchor, body.show .tag-anchor {
    display: block;
    position: fixed;
    height: 30px;
    width: 100vw;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    text-align: center;
    text-indent: 0;
}
body.show .content .content-header {
    bottom: 0;
    box-shadow: 0 1px 14px 0 #00000059;
    z-index: 12;
}
.content.show .tag-anchor .anchor, body.show .tag-anchor .anchor {
    display: none;
}
.content.show .tag-anchor .background-modal, body.show .tag-anchor .background-modal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #00000052;
}
.content-header {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 9;
    background: #fff;
}
.content-header::before {
    content: "";
    position: fixed;
    height: 30px;
    width: 30px;
    background: var(--r-color);
    transform: translateX(-50%);
    top: -30px;
    left: 50%;
    border-radius: 50%;
    display: none;
    transition: .6s;
    z-index: 12;
}
.tag-anchor {
    position: absolute;
    top: -50;
    transition: top .3s;
    text-indent: -9999999999;
    display: none;
}
.tag-anchor .anchor {
    height: 30px;
    width: 30px;
    display: inline-block;
    cursor: pointer;
    border-radius: 15px;
    background: var(--q-color);
    border: 1px solid #dfdfdf;
    box-shadow: 0px 0 7px #0000007a;
}
.tag-anchor .anchor::before, .tag-anchor .anchor::after {
    content: "";
    border-left: 1px solid #000;
    border-bottom: 1px solid #000;
    display: inline-block;
    width: 8px;
    height: 8px;
    position: absolute;
    transform: translate(-50%, 0) rotate(-45deg);
    left: 50%;
}
.tag-anchor .anchor::before {
    top: 7px;
}
.tag-anchor .anchor::after {
    top: 12px;
}
.content-header .search-container {
    height: 100%;
    display: flex;
    flex-direction: column;
}
.content-header .search-wrapper {
    padding: 8px;
    height: 32px;
    overflow: hidden;
    position: relative;
}
.content-header .search-wrapper input {
    border: none;
    background-color: #eeeef0;
    outline: none;
    height: 32px;
    line-height: 32px;
    padding-left: 37px;
    border-radius: 4px;
    width: 100%;
    transition: .2s all;
}
body.show .content-header .search-wrapper input {
    width: calc(100% - 48px);
}
.content-header .search-wrapper button {
    border: none;
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 12px;
    background: transparent;
    cursor: pointer;
    display: none;
    width: 48px;
    height: 32px;
    position: absolute;
    top: 8px;
}
body.show .content-header .search-wrapper button {
    display: inline-block;
}
.content-header .search-wrapper input::placeholder {
    color: #9a9a9a;
}
.content-header .search-wrapper .icons-container {
    position: absolute;
    top: 14px;
    left: 16px;
    width: 20px;
    height: 20px;
    overflow: hidden;
}
.content-header .search-wrapper .icon-search {
    position: relative;
    top: 0;
    width: 50%;
    height: 50%;
    opacity: 1;
    border-radius: 50%;
    border: 3px solid #9a9a9a;
    transition: opacity 0.25s ease, transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
}
.content-header .search-wrapper .icon-search::after {
    content: "";
    position: absolute;
    bottom: -8px;
    right: -4px;
    width: 3px;
    border-radius: 3px;
    transform: rotate(-45deg);
    height: 10px;
    background-color: #9a9a9a;
}
.content-header .search-tab-wrapper {
    display: none;
}
body.show .content-header .search-tab-wrapper {
    display: flex;
    flex-direction: column;
    flex: 1;
}
.content-header .search-tab-header {
    border-bottom: 1px solid #b5b5b5;
}
.content-header .search-tab-header .simplebar-content-wrapper {
    height: 100% !important;
}
.content-header .search-tab-header ul {
    display: inline-block;
    white-space: nowrap;
}
.content-header .search-tab-header li {
    width: 67px;
    text-align: center;
    padding: 10px 0;
    cursor: pointer;
}
.content-header .search-tab-header li.selected-tab {
    color: var(--point-color) !important;
    position: relative;
    overflow: visible;
}
.content-header .search-tab-header li.selected-tab::after {
    content: "";
    width: 80%;
    position: absolute;
    height: 3px;
    left: 50%;
    bottom: -1px;
    border-radius: 2px;
    transform: translate(-50%, 0);
    background: var(--point-color);
}
.content-header .search-tab-body-container {
    display: flex;
    flex-direction: column;
    flex: 1;
}
.content-header .search-tab-body-container .search-tab-body-wrapper>div {
    display: none;
}
.content-header .search-tab-body-container .search-tab-body-wrapper .selected-body{
    display: block;
}
.content-header .selected-container {
    display: flex;
    flex-direction: row;
    padding: 8px;
    border-bottom: 1px solid #cbcbcb;
}
body.show .content-header .selected-container, .content.show .content-header .selected-container {
    border-bottom: none;
}
.content-header .selected-container .selected-wrapper {
    flex: 1;
    overflow: auto;
}
.content-header .selected-container .selected-wrapper {
    white-space: nowrap;
}
.content-header .selected-container .selected-wrapper ul:empty::before {
    content: "모두 선택";
    font-size: .8em;
    line-height: 27px;
    color: #a5a5a5;
}
.content-header .search-tab-body-wrapper{
    flex: 1;
}
.content-header .selection-container .tag-container {
    position: relative;
    height: 100%;
}
.content-header .selection-container .tag-container.open {
    max-height: fit-content;
}
.content-header .selection-container .tag-container .tag-header {
    font-weight: 600;
    padding: 7px 7px;
    position: relative;
    width: 52px;
    min-width: 52px;
    line-height: 35px;
}
.content-header .selection-container .tag-container .tag-wrapper {
    margin: 4px 7px;
    flex: 1;
    overflow: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}
.content-header .selection-container .tag-container .tag-wrapper .tag-grid {
    display: inline-block;
    margin: 4px 0;
}
.content-header .selection-container .tag-container.open .tag-wrapper .tag-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(33%, auto));
    white-space: inherit;
}
.tag-item {
    display: inline-block;
    height: 27px;
    line-height: 27px;
    margin-right: 4px;
    border-radius: 14px;
    padding: 0 8px;
    color: #000;
    font-size: 0.8em;
    cursor: pointer;
}
.tag-grid .tag-item {
    margin: 3px 4px 3px 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.tag-grid .tag-item.contents {
    border: 1px solid var(--w-color);
}
.tag-grid .tag-item.interval {
    border: 1px solid var(--e-color);
}
.tag-grid .tag-item.character {
    border: 1px solid var(--r-color);
}
.open .tag-grid .tag-item {
    width: calc(100% - 20px);
}
.contents-tag {
    background: var(--w-color);
    color: #fff;
}
.interval-tag {
    background: var(--e-color);
    color: #fff;
}
.character-tag {
    background: var(--r-color);
    color: #fff;
}

.content-body {
    padding: 20px 8px;
    margin-top: 83px;
    transition: 0.7s;
}
.content-container .order {
    background: var(--point-color);
    border-radius: 4px;
    color: #fff;
    border: 1px solid transparent;
    padding: 6px;
    cursor: pointer;
}
body.show .content.show .content-container ul {
    height: 99999px;
}
.content-container ul {
    display: grid;
    grid-template-columns: repeat(var(--video-grid-columns), 1fr);
    gap: 10px;
}
.content-container ul li {
    position: relative;
}
.content-wrapper {
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
    overflow-y: clip;
    position: relative;
    height: 100%;
}
.content-wrapper .content-desc {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 8px;
    flex: 1;
}
.content-wrapper .content-desc .title-container {
    display: flex;
    flex: 1;
}
.content-wrapper .content-desc .title {
    flex: 1;
}
.content-wrapper .content-desc .title-container .more {
    display: flex;
    justify-content: flex-end;
    align-items: baseline;
    position: relative;
    overflow: hidden;
    padding: 0 4px;
    margin-left: 13px;
    cursor: pointer;
}
.content-wrapper .content-desc .title-container .dot {
    position: relative;
    width: 3px;
    height: 3px;
    border-radius: 3px;
    background-color: #3f3f3f;
    color: #3f3f3f;
    top: 9px;
}
.content-wrapper .content-desc .title-container .dot::before {
    content: "";
    width: 3px;
    height: 3px;
    border-radius: 3px;
    background-color: #3f3f3f;
    color: #3f3f3f;
    display: inline-block;
    position: absolute;
    top: -7px;
}
.content-wrapper .content-desc .title-container .dot::after {
    content: "";
    width: 3px;
    height: 3px;
    border-radius: 3px;
    background-color: #3f3f3f;
    color: #3f3f3f;
    display: inline-block;
    position: absolute;
    top: 7px;
}
.content-wrapper .content-desc .publishAt {
    text-align: right;
    color: #737373;
    font-size: 0.85em;
}
.content-wrapper .content-desc .tags {
    font-size: .8em;
}
.content-wrapper .content-thumbnail {
    aspect-ratio: 16 / 9;
    background-size: contain;
    border-radius: 7px;
}
.content-wrapper .content-thumbnail a {
    width: 100%;
    height: 100%;
}
.content-container ul li .content-detail {
    display: none;
    position: absolute;
    top: -5px;
    right: -5px;
    left: -5px;
    bottom: -5px;
    padding: 8px 0 8px 8px;
    border-top: 1px solid #e5e5e5;
    z-index: 99;
    word-break: break-word;
    font-size: .8em;
    line-height: 1.4em;
    box-shadow: 0 0 9px #000;
    border-radius: 7px;
    background: #ffffffed;
}
.content-wrapper .content-detail::after {
    content: "〈";
    transform: rotate(-90deg);
    position: absolute;
    right: 18px;
}
.content-container ul li span {
    background: var(--point-color);
    color: #fff;
    font-size: .8em;
    padding: 3px;
    border-radius: 3px;
    margin-right: 5px;
}
.content-container ul li.detail .content-detail {
    display: inline-block;
}